
import Vue from 'vue';

import { mapMutations } from 'vuex';

export default Vue.extend({
  name: 'SnackTestPage',

  data: () => ({
    snackText: '',
  }),

  methods: {
    ...mapMutations(['snackbarMutation']),

    snackTime: function (snack: any) {
      this.snackbarMutation(this.snackText);
      this.$router.push('/Register');
    },
  },
});
